                    :title="tuningHelp.maxEscapeWeight"
                  />
                </div>
                <div class="setting-row">
                  <label :title="tuningHelp.baseEscapeStrength"><br />(baseEscapeStrength):</label>
                  <input
                    type="range"
                    min="0"
                    max="15"
                    step="0.1"
                    v-model.number="systemSettings.baseEscapeStrength"
