// 
function getSpeciesSignature(specList = settings) {
  if (!Array.isArray(specList)) {
    return "";
  }
  return specList
    .map(
      (s, index) =>
        `${index}:${(s && s.count) || 0}:${s && s.isPredator ? 1 : 0}`
